import React, { Component } from 'react';
import Query from './Query';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import { USER } from '../graphql/query/remote';
import toCurrency from '../utils/toCurrency';
import { RENDER_VALUE } from '../utils/commanRender';

const styles = theme => ({
  paper: {
    ...theme.mixins.gutters(),
    paddingTop: theme.spacing.unit * 2,
    paddingBottom: theme.spacing.unit * 2,
  }
});

class User extends Component {
  render() {
    const { classes, user } = this.props;
    return (
      <Query query={USER} variables={{ id: user.id }}>
        {({ loading, error, data: { user } }) => {
          if (loading) {
            return (<span>loading...</span>);
          }
          else if (error) {
            return (<span>Error Occured!</span>);
          }
          return (
            <Paper className={classes.paper}>
              <Grid container spacing={32}>
                <Grid item container direction="column" sm={6}>
                  <Grid item>
                    <Typography variant="headline" className="ttc" color="primary">
                      Username: {user.username}
                    </Typography>
                  </Grid>
                  <Grid item>
                    <Typography variant="headline" className="ttc" color="secondary">
                      Sites: {toCurrency(user.siteCount)}
                    </Typography>
                  </Grid>
                </Grid>
                <Grid item container direction="column" sm={6}>
                  <Grid item>
                    <Typography variant="subheading">
                      Total Received Amount (₹): <span style={{ color: RENDER_VALUE({ value: user.totalReceivedAmount, balance: false }).color }}>{RENDER_VALUE({ value: user.totalReceivedAmount }).value}</span>
                    </Typography>
                  </Grid>
                  <Grid item>
                    <Typography variant="subheading">
                      Total Spent Amount (₹): {toCurrency(user.spent)}
                    </Typography>
                  </Grid>
                  <Grid item>
                    <Typography variant="subheading">
                      Total Balance (₹): <span style={{ color: RENDER_VALUE({ value: user.balance, balance: false }).color }}>{RENDER_VALUE({ value: user.balance }).value}</span>
                    </Typography>
                  </Grid>
                </Grid>
              </Grid>
            </Paper>
          );
        }}
      </Query>
    );
  }
}

export default withStyles(styles)(User);




